import React, { Component } from "react";
import { baseUrl, key } from "./config";
import _ from "lodash";
import axios from "axios";

class Imovel extends Component {
  state = {
    imovel: {
      Foto: [{ Foto: "", FotoPequena: "" }]
    },
    fields: [
      "Codigo",
      "Categoria",
      "Bairro",
      "ValorVenda",
      "ValorLocacao",
      "AreaTotal",
      "AreaPrivativa",
      "Dormitorios",
      "Vagas",
      "Status",
      "Caracteristicas",
      "InfraEstrutura",
      "Latitude",
      "Longitude",
      "FotoDestaque",
      { Foto: ["Foto", "FotoPequena"] }
    ]
  };
  getImovel = async codigo => {
    const fields = JSON.stringify(this.state.fields);
    const getImovel = await axios.get(
      `${baseUrl}/imoveis/detalhes?key=${key}&imovel=${codigo}&pesquisa={"fields":${fields}}`
    );
    const imovel = getImovel.data;
    imovel.Foto = _.values(imovel.Foto);
    console.log(imovel);
    this.setState({ imovel });
  };
  componentDidMount() {
    this.getImovel(this.props.match.params.codigo);
  }
  render() {
    const { imovel } = this.state;
    return (
      <div className="imovel my-5">
        <div className="container">
          <div className="row">
            <div className="col-xl-8">
              <img src={this.state.imovel.FotoDestaque} alt="" />
            </div>
            <div className="col-xl-4">
              <div className="row">
                {imovel.Foto.map(foto => {
                  return (
                    <div className="col-6">
                      <img src={foto.FotoPequena} />
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Imovel;
